import React from 'react';
import { Radio, Tabs } from 'antd';
import './App.css';
import VirtualizedListCustom from './examples/VirtualizedListCustom';
import InfiniteScrollList from './examples/InfiniteScrollList';
import LazyImage from './examples/LazyImage';
import DynamicInfiniteList from './examples/DynamicInfiniteList';
import RefreshOnVisable from './examples/RefreshOnVisable';
import BigDataListExample from './examples/BigDataListExample';


const items = [
  { key: 'BigDataListExample', label: '', content: <BigDataListExample /> },
  { key: 'InfiniteScrollList', label: '', content: <InfiniteScrollList /> },
  { key: 'LazyImage', label: '', content: <LazyImage /> },
  { key: 'DynamicInfiniteList', label: '', content: <DynamicInfiniteList /> },
  { key: 'RefreshOnVisable', label: '', content: <RefreshOnVisable /> },
  { key: 'VirtualizedListCustom', label: 'API', content: <VirtualizedListCustom /> },
]

const App: React.FC = () => {
  const { TabPane } = Tabs;
  return (
    <div className="App">
      <div className="title-class"></div>
      <Tabs defaultActiveKey={items[0].key} tabPosition="left">
        {
          items.map(item => {
            return (
              <TabPane tab={item.label} key={item.key}>
                {item.content}
              </TabPane>
            )
          })
        }
      </Tabs>
    </div>
  );
}

export default App;
