import React, { useState, useEffect } from 'react';
import VirtualizedList from 'react-virtualized-list';
import './style/common.css';

interface Article {
  id: number;
  title: string;
  content: string;
}

interface ArticleData {
  title: string;
  content: string;
}

const fetchArticleData = async (article: Article): Promise<ArticleData> => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({ title: `${article.title}`, content: ` ${new Date().toLocaleTimeString()}` });
    }, 500);
  });
};

const RefreshOnVisible: React.FC = () => {
  const [articles, setArticles] = useState<Article[]>([]);

  useEffect(() => {
    const nowDate = new Date().toLocaleTimeString();
    const initialArticles = Array.from({ length: 100 }, (_, i) => ({ id: i, title: `item ${i}`, content: ` ${nowDate}` }));
    setArticles(initialArticles);
  }, []);

  return (
    <div className='container'>
      <div className='title'>
        <h2></h2>
        <p></p>
        <p> `refreshOnVisible`<a href='https://github.com/SailingCoder/react-virtualized-list/blob/main/src/examples/RefreshOnVisable.js' target='_blank'>RefreshOnVisable</a></p>
      </div>
      <div className='content'>
        <VirtualizedList<Article>
          listData={articles}
          renderItem={(article, data) => (
            <div>
              <h2>{data ? data.title : 'Loading...'}</h2>
              <p>{data ? data.content : 'Loading...'}</p>
            </div>
          )}
          fetchItemData={fetchArticleData}
          refreshOnVisible={true}
          itemClassName='item-class-refresh'
          containerHeight='500px'
          itemLoader={<>Loading article...</>}
        />
      </div>
    </div>
  );
};

export default RefreshOnVisible;
